.copyright {
  font-size: 12px;
  color: var(--system-text-color, rgb(44 52 55 / 1));
  opacity: 0.45;
  line-height: 16px;
  text-align: center;
  padding: 8px 0;
  user-select: none;
  span {
    display: none;
    &:nth-child(1){
      display: initial;
    }
  }
  &:hover {
    span {
      &:nth-child(1){
        display: none;
      }
      &:nth-child(2){
        display: initial;
      }
    }
  }
}
