.container {
  height: 100%;
  .panel {
    height: 100%;
    padding: 0 32px;
    .item {
      display: inline-flex;
      min-height: 28px;
      width: 100%;
      align-items: center;
      margin: 8px 0;
      .title {
        font-size: 14px;
        // color: rgba(0,0,0,0.85);
        color: var(--system-text-color, #000);
        opacity: 0.85;
        flex-shrink: 0;
        .question {
          font-size: 12px;
          // color: rgba(0,0,0,0.45);
          color: var(--system-text-color, #000);
          opacity: 0.45;
          margin-left: 4px;
          cursor: pointer;
        }
      }
      .action {
        width: 100%;
        margin-left: 8px;
      }
      &.btns {
        flex-direction: column;
        :global {
          .ant-upload-wrapper,
          .ant-upload,
          .ant-btn {
            width: 100%;
          }
          .ant-upload-wrapper {
            margin-bottom: 8px;
          }
        }
      }
      :global {
        .ant-slider {
          margin: 8px 5px;
        }
      }
    }
    .w50 {
      width: 50%;
    }
  }
}
