.logoView {
  padding: 32px 0 8px 0;
  .iconContainer {
    text-align: center;
  }
  .titleContainer {
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    color: var(--system-text-color, rgba(0, 0, 0, 1));
    opacity: 0.85;
    font-weight: 400;
    & + .subTitleContainer {
      margin-top: 4px;
    }
  }
  .subTitleContainer {
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: var(--system-text-color, rgba(0, 0, 0, 1));
    opacity: 0.85;
    font-weight: 300;
  }
}
